<template>
    <section class="main-section blog-section">

        <div class="container">
            <div class="row">
                <div class="col-lg-6 pb-5 pt-5">
                    <img v-bind:src="'../'+blog.path"   alt="">
                </div>

            </div>

            <a class="category color-green" href="#">{{blog.category}}</a>
            <h3 class="margin-tb-10">{{blog.title}}</h3>
            <h6 class="margin-b-30"><span class="color-light-black"></span><a class="color-black" href="#">{{blog.user}}</a></h6>
            <p class="margin-b-10" v-html="blog.content"></p>
        </div><!-- left-area -->


    </section><!-- main-section -->
</template>

<script>
    import axios from "axios";


    export default {
        name: "blog",
        data(){
            return {
                blog:null,
            }
        },
        mounted() {
            let scriptArray=[
                '../assets/common-js/jquery-3.2.1.min.js',
                '../assets/common-js/tether.min.js',
                '../assets/common-js/bootstrap.js',
                '../assets/common-js/scripts.js'


            ];
            for (var key in scriptArray){
                let recaptchaScript = document.createElement('script')
                recaptchaScript.setAttribute('src', scriptArray[key])
                document.getElementById('app').appendChild(recaptchaScript)
            }

            axios.defaults.headers.common['Access-Control-Allow-Origin'] = 'http://localhost:3000';
            axios.get('https://hamidkhani.github.io/taghbostan/dist/api/blog/'+this.$route.params.slug)
                .then(response => {

                    this.blog = response.data[0];


                })


        },
    }
</script>

<style  lang="css">
    @import '../../public/assets/06_Regular_Page/css/styles.css';

</style>

<style lang="css">

    @import '../../public/assets/06_Regular_Page/css/responsive.css';

</style>
<style lang="css">

    @import '../../public/assets/css/custom_style.css';
</style>