<template>
    <section class="main-section">
        <div class="p-grid-isotope">

            <div v-for="image in gallery"  v-bind:key="image.id" class="p-item grid-sizer"><a class="venobox"  v-bind:href="'../'+image.path" >
                <img  v-bind:src="'../'+image.path" alt=""/></a></div>



        </div><!-- p-grid -->
    </section><!-- main-section -->
</template>

<script>
    import axios from "axios";


    export default {
        name: "Gallery",
        data(){
            return {
                gallery:null,
            }
        },
        mounted() {
            let scriptArray=[
                '../assets/common-js/jquery-3.2.1.min.js',
                '../assets/common-js/tether.min.js',
                '../assets/common-js/bootstrap.js',
                '../assets/common-js/isotope.pkgd.min.js',
                '../assets/common-js/venobox.min.js',
                '../assets/common-js/isotope.pkgd.min.js',
                '../assets/common-js/scripts.js'


            ];
            for (var key in scriptArray){
                let recaptchaScript = document.createElement('script')
                recaptchaScript.setAttribute('src', scriptArray[key])
                document.getElementById('app').appendChild(recaptchaScript)
            }

            axios.defaults.headers.common['Access-Control-Allow-Origin'] = 'http://localhost:3000';
            axios.get('https://hamidkhani.github.io/taghbostan/dist/gallery/'+this.$route.params.slug)
                .then(response => {

                    this.gallery = response.data;


                })


        },
    }
</script>

<style  lang="css">
    @import '../../public/assets/common-css/venobox.css';

</style>

<style  lang="css">
    @import '../../public/assets/02_Portfolio/css/styles.css';

</style>

<style lang="css">

    @import '../../public/assets/02_Portfolio/css/responsive.css';

</style>
<style lang="css">

    @import '../../public/assets/css/custom_style.css';
</style>