<template>
    <section class="main-section">



        <div class="blog-area" v-for="gallery in galleries" v-bind:key="gallery.id">
            <a class="blog-post full-height" v-bind:style="{backgroundImage:'url(../'+gallery.path+')'}" v-bind:href="'gallery/'+gallery.slug" >
                <img v-bind:src="'../'+gallery.path" alt="">
                <div class="blog-info">
                    <h6 class="color-green margin-b-5">{{gallery.category}}</h6>
                    <h3>{{gallery.title}}</h3>
                </div><!-- blog-info -->
            </a><!-- blog-post -->
        </div><!-- blog-area -->


    </section><!-- main-section -->
</template>

<script>
    import axios from "axios";

    export default {
        name: "galleries",
        data(){
            return {
                galleries:[],
            }
        },
        mounted() {
            let scriptArray=[
                '../assets/common-js/jquery-3.2.1.min.js',
                '../assets/common-js/tether.min.js',
                '../assets/common-js/bootstrap.js',
                '../assets/common-js/scripts.js'


            ];
            for (var key in scriptArray){
                let recaptchaScript = document.createElement('script')
                recaptchaScript.setAttribute('src', scriptArray[key])
                document.getElementById('app').appendChild(recaptchaScript)
            }

            axios.defaults.headers.common['Access-Control-Allow-Origin'] = 'http://localhost:3000';
            axios.get('https://hamidkhani.github.io/taghbostan/dist/api/galleries/')
                .then(response => {

                    this.galleries = response.data;


                })


        },
    }
</script>

<style >
    @import '../../public/assets/04_Blog/css/styles.css';
</style>

<style >
    @import '../../public/assets/04_Blog/css/responsive.css';
</style>

<style >
    @import '../../public/assets/css/custom_style.css';
</style>